import React, { useEffect } from "react";
import { useState } from "react";
import "../styling/chat.css";
import DoubleArrowIcon from "@mui/icons-material/DoubleArrow";
import { Button } from "@mui/material";
import { Avatar } from "@mui/material";
import axios from "axios";

const Chat = () => {
  const [messages, setMessages] = useState([
    {
      content: "Hello! This is Edith. How can I assist you?",
      role: "assistant",
    },
  ]);
  const [typedMessage, setTypedMessage] = useState("");
  const [sendDisabled, setSendDisabled] = useState(true);
  const handleSend = () => {
    console.log(
      typedMessage.toLowerCase() == "what is your name",
      typeof typedMessage
    );

    setMessages((prev) => [...prev, { content: typedMessage, role: "user" }]);
    if (
      typedMessage.toLowerCase() == "what is your name" ||
      typedMessage.toLowerCase() == "what's your name"
    ) {
      setMessages((prev) => [
        ...prev,
        {
          content:
            "I'm Edith, a chat bot assistant, built by Kalyan using OpenAI api",
          role: "assistant",
        },
      ]);
    } else {
      axios
        .post("http://localhost:8080/chat", {
          messages: [{ content: typedMessage, role: "user" }],
        })
        .then((res) => setMessages((prev) => [...prev, res.data]))
        .catch((err) => {
          console.log(err);
        });
    }
    setTypedMessage("");
    console.log(messages);
  };

  const handleKeyDown = (e) => {
    if (e.key === "Enter" && !sendDisabled) {
      handleSend();
    }
  };

  const getAvatar = (type) => {
    if (type === "user") {
      return <Avatar alt="User" src="shs" />;
    } else if (type === "assistant") {
      return (
        <Avatar
          alt="Bot"
          src="data:image/jpeg;base64,"
        />
      );
    }
    return null;
  };
  useEffect(() => {
    if (typedMessage.trim().length == 0) {
      setSendDisabled(true);
    } else {
      setSendDisabled(false);
    }
  }, [typedMessage]);

  return (
    <div className="container">
      <div className="title">EDITH</div>
      <div className="chatwindow">
        <div className="chat">
          <div className="display-messages">
            {messages.map((message, index) => (
              <div
                style={{
                  display: "flex",
                  flexDirection: "row",
                  gap: "10px",
                  maxWidth: "400px",
                  // justifyContent: "baseline",
                  height: "fitcontent",
                  marginBottom: "10px",
                  alignItems: "baseline",
                  padding: "10px",
                  borderRadius: "5px",
                }}
                key={index}
                className={`message ${
                  message.role === "user" ? "user-message" : "bot-message"
                }`}
              >
                {message.role !== "user" ? (
                  <div className="">{getAvatar(message.role)}</div>
                ) : (
                  <div>{getAvatar(message.role)}</div>
                )}
                <div
                  className={` ${
                    message.role === "user" ? "user-message" : "bot-message"
                  }`}
                >
                  {message.content}
                </div>
                {/* {message.type == "user" && (
                <div className="avatar">{getAvatar(message.type)}</div>
              )} */}
              </div>
            ))}
          </div>

          <div className="message-button">
            {" "}
            <input
              type="text"
              className="messagebox"
              placeholder="Type here..."
              value={typedMessage}
              onChange={(e) => {
                setTypedMessage(e.target.value);
              }}
              onKeyDown={handleKeyDown}
            ></input>
            <Button
              variant="contained"
              className="send-button"
              onClick={handleSend}
              disabled={sendDisabled}
              sx={{ background: "white", borderRadius: "30px" }}
            >
              <DoubleArrowIcon
                color="primary"
                sx={{ fontSize: "30px", width: "50px" }}
              />
            </Button>
          </div>
        </div>
      </div>
    </div>
  );
};

export default Chat;
